import React, {useEffect, useState} from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import "./App.css";
import {Unsplash} from "./Unsplash/Unsplash";


const App = () => {
	const [responsePhotos, setResponsePhotos] = useState(null);

	useEffect(() => {
		Unsplash.photos.getRandom({count: 1,})
			.then(result => {
				console.log(result.response[0].links.download);
				setResponsePhotos(result.response[0].links.download);
			});
	}, []);

	return (
		<div className="App">
			<img className='image' src={responsePhotos}/>
		</div>
	);
};

export default App;
